<template>
    <div class="block">
        <div class="container">


            <form @submit="$event.preventDefault()">
                <div class="formLogin">
                    <div class="form-group">
                        <label for="email">Email :</label>
                        <input id="email" class="form-control" type="email" v-model="form.email" required
                               placeholder="Enter email">
                    </div>
                    <div class="form-group">
                        <label for="password">Password :</label>
                        <div class="input-group mb-3">
                            <input id="password" class="form-control" :type="type" v-model="form.password" required
                                   placeholder="Enter password" aria-describedby="passwordHelp">
                            <div class="input-group-append"><a class="eye"
                                                               @click="type === 'text' ? type = 'password' : type = 'text'">
                                <!--Ternary function-->
                                <font-awesome-icon :icon="type === 'text' ? 'eye' : 'eye-slash'"/>
                            </a></div>
                            <!--Ternary function-->
                        </div>
                    </div>
                    <div class="bouton">
                        <button class="btn btn-primary btn-lg rounded" name="submit" value="Submit" type="submit"
                                @click="login($event)">Submit
                        </button>
                    </div>
                </div>
            </form>

        </div>

    </div>
</template>

<script>
    import axios from 'axios';
    import auth from '../utils/auth.js'
    export default {
        name: 'login',
        data() {
            return {
                icon: 'eye',
                type: 'password',
                btnText: 'Show Password',
                password: '',
                form: {
                    email: '',
                    password: '',
                }
            }

        },
        methods: {
            login() {
                axios.post('/api/login', this.form) //TODO change http
                    .then(response => {
                        auth.setLocalToken(response.data);
                        this.$router.push('/');
                        this.reloadPage();
                        console.log('gg', response);
                    })
                    .catch(error => {
                        console.log('nul', error)
                    });
                    
            },
            reloadPage(){
                window.location.reload()
            },

        }
    }

</script>
<style scoped>
    form {
        text-align: left;
    }

    .bouton {
        text-align: center;
    }

    .formLogin {
        width: 50%;
        margin: auto;
    }

    .eye {
        padding-top: 7px;
        padding-left: 4px;
        padding-right: 4px;
        border: 0.1em solid #CED4DA;
    }

</style>
